import React from 'react'
import '../styles/Projects.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGithub, faLinkedinIn, faMailchimp } from "@fortawesome/free-brands-svg-icons"
function Projects() {
    return (
        <div className="projects" style={{ marginBottom: "30vh" }}>
            <div className="back">
                <h1>  Apps I've Built</h1>

                <p>Here are a few projects upon which I've worked on recently.Click on any project to go to that Website.Hover to get a brief information about the project. <br /><br /><br /> Want to see more? Checkout My github Repo.<FontAwesomeIcon icon={faGithub} size='3x' /></p>

            </div>
            <div className="container">
                <div className="project"></div>
                <div className="project"></div>
                <div className="project"></div>
                <div className="project"></div>
                <div className="project"></div>
                <div className="project"></div>
                <div className="project"></div>
                <div className="project"></div>
            </div>
        </div>
    )
}

export default Projects
