import React from 'react'
import { StaticImage } from "gatsby-plugin-image"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faGithub, faLinkedinIn, faMailchimp } from "@fortawesome/free-brands-svg-icons"
import { faEnvelope, faCode } from "@fortawesome/free-regular-svg-icons"
import '../styles/Header.scss'
function Header() {
    // function handleMode(){
    //     do
    // }
    return (
        <div >
            <header className="head">
                {/* <img src="https://img.icons8.com/material-outlined/24/000000/logo--v2.png"/> */}
                <div className="logo">
                    <StaticImage
                        className="log"
                        src="../images/PGLOGOPURPLE1.svg"
                        alt="Night/Light"
                        placeholder="blurred"
                        layout="fixed"
                        width={40}
                    // height={40}
                    />
                </div>
                <div className="links">
                    <ul className="linksHead">
                        <li>Home</li>
                        <li>About</li>
                        <li>Skills</li>
                        <li>Projects</li>
                        <li>Contact Me</li>
                    </ul>
                    <div className="mode">
                        <StaticImage
                            src="../images/moon.svg"
                            alt="Night/Light"
                            placeholder="blurred"
                            layout="fixed"
                            width={40}
                            height={40}
                        />
                    </div>
                </div>
                <ul className="social">

                    <li className="icons"> <FontAwesomeIcon icon={faGithub} size='2x' /> </li>
                    <li className="icons"> <FontAwesomeIcon icon={faLinkedinIn} size='2x' /> </li>
                    <li className="icons"> <FontAwesomeIcon icon={faEnvelope} size='2x' /></li>

                </ul>

            </header>
        </div>
    )
}

export default Header
